.del {
  background-color: #FF0000;
  color: #FFF;
}
.product > img {
  display: block;
  width: 100%;
  height: 4.5rem;
  max-height: 500px;
}
.product .extra {
  display: flex;
  color: #AEAEAE;
  padding: 8px 0;
}
.product .extra > span {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product .extra > span > i {
  margin-right: 4px;
}
